$bg: rgb(16,16,16);

.banner{
    height:600px;
    background-color: $bg;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 3rem;
    box-sizing: border-box;
    &::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top:0;
        left:0;
        background: $bg;
        opacity: 0.4;
    }
    
    h1,p{
        color:white;
        font-size: 3rem;
        width: 40%;
        z-index: 10;
        
    }
    p{
        font-size: 1.2rem; 
        letter-spacing:2px ;
        word-spacing:3px ;
        font-weight:100 ;
        word-spacing: 5px;
    }
    >div{
        display: flex;
        
        button{
            padding:1rem ;
            width: 150px;
            outline:none ;
            z-index: 10;
            border-radius: 5px;
            cursor: pointer;
            margin: 1rem;
            border: none;
            background-color: white;
            font-size: 1.2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.5s;
            
            &:hover{
                opacity: 0.6;
            }
            
            &:nth-child(even){
                background-color: rgb(76,76,76);
                color: rgb(255,255,255);
            }
            &:nth-child(odd){
                background-color: rgb(207, 197, 197);
                color: black;
            }
            svg{
                font-size: 1.7rem;
                margin: 0 10px;
                
            }
            
        }
        
    }
}
    

.row{
    padding: 1rem;
    background-color: $bg;
    >h2{
        margin:0.3rem;
        font-weight: 400;
    }
    > div{
        display:flex;
        overflow-x: auto;
       
    }
}
.card{
    width:100px;
    margin: 0.3rem;
    cursor: pointer;
    transition: transform 0.5s;

    &:hover{
        transform: translateY(-10px);
    }
}
.genreBox{
    overflow-x: auto;
    display: flex;
    height: 100px;
    background-color: $bg;
    flex: 0%;
    >a{
        color:black;
        text-align: center;
        background-color: white;
        padding: 1rem 0;
        border-radius: 5px;
        transition: all 0.5s;
        min-width: 10rem;
        margin: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover{
           background-color: rgb(236,236,236); 
        }
    }
}